"use client";

import React, { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import Lottie from "lottie-react";
import animationData from "./Animation - 1748181041132.json";
const Page = () => {
  const route = useRouter();
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const timer = setTimeout(() => setLoading(false), 1000);
    return () => clearTimeout(timer);
  }, []);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Lottie animationData={animationData} loop={true} />
      </div>
    );
  }
  return (
    <div className="flex items-start justify-center gap-10 min-h-screen p-10 ">
      <button onClick={() => route.push("/")}>
        <Image
          src={"/assets/shared/icon-arrow-left.svg"}
          width={8}
          height={4}
          alt="arrow left"
        />
        Go Back
      </button>
      <div>
        <Image
          src={"/assets/shared/icon-new-feedback.svg"}
          width={56}
          height={56}
          alt="arrow left"
        />
        <h1>Create New Feedback</h1>

        <form action="">
          <div>
            <label htmlFor="title">Feedback Title</label>
            <p>Add a short, descriptive headline</p>
            <input type="text" name="title" id="title" />
          </div>{" "}
          <div>
            <label htmlFor="category">Category</label>
            <p>Choose a category for your feedback</p>
            <input type="text" name="category" id="category" disabled />
          </div>{" "}
          <div>
            <label htmlFor="Detail">Feedback Title</label>
            <p>Add a short, descriptive headline</p>
            <textarea name="Detail" id="Detail"></textarea>
          </div>
          <div className="btnContainer">
            <button onClick={() => route.push("/")}>Cancel</button>
            <button type="submit">Add Feedback</button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Page;
